import { VideoDataList } from "../../client";
import * as Actions from "./actions";

export const initialState: VideoDataList = {
    videoDataList: undefined
};

export const VideoListReducer = (state: VideoDataList = initialState, action: Actions.ActionType) => {
    switch(action.type) {
    case Actions.FETCH_VIDEO_LIST:
        return {
            ...action.payload
        };
    default:
        return state;
    }
};